.list-row {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.5rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0.5rem;
    padding: 0.5rem;
    border-radius: 1rem;
    /* background-color: #fff */
    ;
  }
  
  .list-row:hover {
    background-image: linear-gradient(to right, var(--darkpink), var(--darkblue));
    transition: all 0.3s;
  }
  
  
  .list-row td {
    width: 18%;
    color: black;
  }
  
  .td-img {
    width: min-content !important;
  }
  
  .mobile-td-cap {
    display: none;
  }
  @media only screen and (max-width: 1000px) {
    .td-img img {
      width: 2rem !important;
      height: 2rem !important;
    }
  
    .price-chip-list {
      min-width: 2rem !important;
      font-size: 0.6rem !important;
      border-width: 2px !important;
      padding: 0.25rem 0.5rem !important;
    }
    .trending-icon-list {
      display: none !important;
    }
  }
  @media only screen and (max-width: 800px) {
    .list-row {
      margin-top: 1rem;
      width: 90%;
      gap: 0rem !important;
    }
    .td-mkt-cap {
      display: none !important;
    }
    .mobile-td-cap {
      display: block;
    }
    .coin-price-list {
      font-size: 0.8rem !important;
    }
    .coin-symbol-list,
    .coin-name-list,
    .coin-total_volume-list {
      font-size: 80% !important;
    }
  }